<template>
  <div class="marketDetail">
    <van-nav-bar
      title="继续跟进"
      left-arrow
      :fixed="true"
      @click-left="onClickLeft"
    />
    <div style="height: 12.26667vw"></div>
    <div style="padding: 0 10px">


      <van-form @submit="onSubmit">
        <van-field
            v-model="name"
            rows="3"
            autosize
            label="客户名称"
            type="text"
            placeholder="请输入客户名称"
             :rules="[{ required: true, message: '' }]"
            />
          <van-field
          clickable
          name="picker"
          v-model="follow_type_name"
          label="意向等级"
          placeholder="选择意向等级"
          @click="pickType = true"
          :rules="[{ required: true, message: '' }]"
        />
         <van-field
          clickable
          name="picker"
          v-model="follow_status_name"
          label="跟进状态"
          placeholder="选择跟进状态"
          @click="pickStatus = true"
          :rules="[{ required: true, message: '' }]"
        />
        <van-field
          clickable
          name="datetimePicker"
          v-model="next_follow_time"
          label="下次跟进时间"
          placeholder="选择跟进时间"
          @click="showPicker = true"
          :rules="[{ required: true, message: '' }]"
        />
        <van-field name="feature" label="客户特征" :rules="[{ required: true, message: '请选择客户特征' }]">
            <template #input>
                <van-radio-group v-model="feature" direction="horizontal">
                <van-radio @click="TipsTap(0)" name="1">鸽子型</van-radio>
                <van-radio @click="TipsTap(1)" name="2">孔雀型</van-radio>
                <van-radio @click="TipsTap(2)" style="margin-top:10px;" name="3">猫头鹰型</van-radio>
                <van-radio @click="TipsTap(3)" style="margin-top:10px;" name="4">老虎型</van-radio>
                </van-radio-group>
            </template>
        </van-field>
         <van-field
            v-model="follow_remark"
            rows="3"
            autosize
            label="跟进备注"
            type="textarea"
            placeholder="跟进备注"
             :rules="[{ required: true, message: '' }]"
            />
        <div style="margin: 16px">
          <van-button round block type="info" native-type="submit">
            保存
          </van-button>
        </div>
      </van-form>

      <van-popup v-model="showPicker" position="bottom">
            <van-datetime-picker
                type="date"
                title="选择年月日"
                @confirm="onConfirm"
                @cancel="showPicker = false"
            />
      </van-popup>

       <van-popup v-model="pickType" position="bottom">
          <van-picker
            show-toolbar
            :columns="columns"
            @confirm="onConfirm1"
            value-key="desc"
            @cancel="pickType = false"
          />
       </van-popup>

       <van-popup v-model="pickStatus" position="bottom">
          <van-picker
            show-toolbar
            :columns="columns1"
            @confirm="onConfirm2"
            value-key="label"
            @cancel="pickStatus = false"
          />
       </van-popup>

    </div>
  </div>
</template>

<script>
import { Toast, NavBar, Icon, Button, Form, Field,DatetimePicker,Popup,Radio,RadioGroup,Picker,Dialog} from "vant";

export default {
  components: {
    [NavBar.name]: NavBar,
    [Icon.name]: Icon,
    [Button.name]: Button,
    [Form.name]: Form,
    [Field.name]: Field,
    [DatetimePicker.name]: DatetimePicker,
    [Popup.name]: Popup,
    [RadioGroup.name]: RadioGroup,
    [Radio.name]: Radio,
    [Picker.name]:Picker,
    [Dialog.name]:Dialog
  },
  filters: {
    _GetDateStr(sj_str) {
      var iday = 0;
      try {
        var sjdate = new Date(sj_str).toLocaleDateString();
        var sjdatedd = Date.parse(sjdate) / 1000;
        if (sjdatedd == isNaN) {
          return "未知";
        }
        // eslint-disable-next-line no-console
        console.log(sjdatedd);
        var date = new Date().toLocaleDateString();
        var dddate = Date.parse(date) / 1000;
        iday = Math.floor(parseInt(dddate - sjdatedd) / 60 / 60 / 24);
      } catch (err) {
        return "未知";
      }

      if (-1 == iday) {
        return "今天";
      } else if (0 == iday) {
        return "昨天";
      } else {
        var dd1 = [];
        try {
          var dd2 = new Date(sj_str).toLocaleDateString();
          if (dd2 == "Invalid Date") {
            return "未知";
          }
          dd1 = dd2.split("/");
          // eslint-disable-next-line no-console
          console.log("dsfsdf");
          // eslint-disable-next-line no-console
          console.log(dd1);
        } catch (err) {
          return "未知";
        }

        var y = dd1[0];
        var m = dd1[1]; //获取当前月份的日期
        var d = dd1[2];
        return y + "-" + (m < 10 ? "0" + m : m) + "-" + (d < 10 ? "0" + d : d);
      }
    },
  },
  data() {
    return {
      name:"",
      follow_type:"",
      follow_type_name:"",
      follow_status:"",
      follow_status_name:"",
      next_follow_time:"",
      feature:"1",
      follow_remark:"",
      deal_money:0,
      loading: false,
      finished: false,
      page: 1,
      pageSize: 20,
      showPicker: false,
      pickType:false,
      pickStatus:false,
      columns:[],
      columns1:[{label:"跟进中",value:"running"},{label:"已成交",value:"dealed"},{label:"已放弃",value:"give_up"}],
      TipsList:[{
        title:"鸽子型",
        content:"性格特点：友好镇静，不急不躁，多疑，缺乏安全感，按程序办事，不喜欢冲突，属于理解性客户；<br/>电话中的声音特点：语速不快，音量不大，需要引导，音调有变化，回答问题不慌不忙；<br/>如何与他们电话沟通：要显得镇静，不可急躁。讲话语速要慢，音量不要太高，显示友好和平，要柔声细语（即使想要发火，语气也要温柔的像鸽子），建立关系需要一定的时间，帮助客户做事，建立信任。他们做决定没有主见，常常会说问一下朋友。所以我们要帮客户做决定并且要给他建立信心，让客户相信我们的决定。"
      },
      {
        title:"孔雀型",
        content:"性格特点：做事爽快，决策果断，沟通能力强，以人为中心，不以任务为中心。一群人在一起，他们成为交谈的中心，有丰富的面部表情，喜欢在一种友好的环境下交流，追求被认可，希望不辜负他人的期望，得到别人的喜欢尤为重要；<br/>电话中的声音特点：讲话很快，音量也很大，音调富有变化，表现很热情，对你的态度也很友好，也有可能在电话里听到客户爽朗的笑声，经常主动提出自己的看法，对你讲的话反应也很快，有时也会打断你；<br/>如何与他们电话沟通：他们看中关系，对人也很热情，所以电话销售人员在电话中向客户传递你很重要，这样的信息对吸引他们来讲显得很重要，经常保持联系，表现非常重视客户。当他提出想法，不要直接否定他，让他继续讲完，如果你有更好的建议，可以先认可，然后告诉他我这有一个解决方案，我和您说下，您也可以做个参考。这类客户和你合作是看中关系和感觉，适合月底求单。"
      },
      {
        title:"猫头鹰型",
        content:"性格特点：很难看得懂，不太容易向对方表示友好，高冷也不爱说话，工作认真，讨厌不细致马虎的工作态度，做事动作也缓慢，做决策也很慢，他们更喜欢通过大量的事实数据；来做判断，以确保他们做的是正确的事情，猫头鹰客户显得孤僻；<br/>电话中的声音特点：讲话不快，音量也不大，音调变化也不大，电话中并不太配合销售工作，不管你说什么都说：嗯、啊，显得无从下手；<br/>如何与他们电话沟通：电话前要准备充足，电话交流要认真，不可马虎要仔细，注意平时不太注意的细节，不可与他们讨论太多与目的无关的事情，不要显得太过热情，他们会觉得不适应，要直入主题，多花心思，客户如果愿意和你交流，我们多提供数据，用细节和利益吸引客户。"
      },
      {
        title:"老虎型",
        content:"性格特点：做事爽快，决策果断，以事实和任务为中心，对他们的印象就是不善于和人打交道，被认为全力派人物，喜欢支配及下命令，时间观念很强，讲究高效率，喜欢直入主题，不喜欢花时间交流；<br/>电话中的声音特点：讲话快，不喜欢浪费时间，严肃冷淡，会问：什么事？你是干嘛的？一旦你和他们建立起信任关系，他们喜欢讲而不是听；<br/>如何与他们电话沟通：直入主题，不要啰嗦，讲利益，有什么好处，说一不二，不要下命令，不喜欢被操控，直接讲目的，主动打电话并且做好充足的准备，他们提出的问题都希望得到非常专业和非常棒的解答。"
      }
      ]
    };
  },
  created() {
    // this.findSerachList();
    let that = this;
    this.id = this.$route.query.id
    this.get_type_data().then(result=>{
        that.columns = result;
        this.findSerachList();
    })
  },
  methods: {
    TipsTap(index){
      let curTips = this.TipsList[index];
      Dialog.alert({
        title: curTips.title,
        message: curTips.content,
      }).then(() => {
        // on close
      });
    },
     onConfirm1(value) {
       
       this.pickType = false;
       this.follow_type_name = value.desc;
       this.follow_type = value.type;
       // eslint-disable-next-line no-console
       console.log(value,"onConfirm1");
     },
     onConfirm2(value) {
       this.pickStatus = false;
       this.follow_status_name = value.label;
       this.follow_status      = value.value;
       // eslint-disable-next-line no-console
       console.log(value);
     },
     onConfirm(time) {
         // eslint-disable-next-line no-console
      let timeTmp = new Date(time);
      this.next_follow_time = timeTmp.getFullYear() +"-"+(timeTmp.getMonth()+1<10?'0'+(timeTmp.getMonth()+1):timeTmp.getMonth()+1)+"-"+(timeTmp.getDate()<10?'0'+timeTmp.getDate():timeTmp.getDate());
      this.showPicker = false;
    },
    onSubmit(){
       let that = this;
       if(this.follow_status == 'dealed' && !this.deal_money)
        {
            Toast('成交金额不能为空');
            return;
        }

        let data =  {
            name:this.name,
            feature:this.feature,
            customer_id:this.id,
            status:this.follow_status,
            type:this.follow_type,
            remark:this.follow_remark
          }
          if(this.next_follow_time){
            data.next_follow_time = this.next_follow_time;
          }

          if(this.follow_status == 'running' && !this.next_follow_time)
          {
              Toast('下次跟进时间不能为空');
              return;
          }
        
          if(this.deal_money){
             data.deal_money = this.deal_money;
          }
       
        this.$http
          .post("/admin/business/continue_follow",data)
          .then(function (response) {
              if (response.data.code === 2000 && response.data.data) {
                  // 数据全部加载完成purpose  source
                  Toast('跟进成功');
                  that.$router.push("/");
              }else{
                 Toast(response.data.data);
              }
          })
          // eslint-disable-next-line no-unused-vars
          .catch(function (error) {
               Toast('网络错误');
          });


    },
    get_type_data(){
        let that = this;
        return new Promise(function(resolve){
            that.$http
                .post("/admin/business/get_type", {})
                .then(function (response) {
                // eslint-disable-next-line no-console
                     console.log(response.data.data.purpose);
                    if (response.data.code === 2000) {
                       // 数据全部加载完成purpose  source
                        if(response.data.data){
                            resolve(response.data.data.purpose || []);
                        }else{
                            resolve([]);
                        }
                    }
                })
                // eslint-disable-next-line no-unused-vars
                .catch(function (error) {
                     resolve([]);
                });
        });
    },
     findSerachList() {
      let that = this;
      this.$http
        .post("/admin/business/follow_log", {
            customer_id:this.id
        })
        .then(function (response) {
          // eslint-disable-next-line no-console
          console.log(response.data);
          if (response.data.code === 2000) {
            //   // 数据全部加载完成purpose  source
             if(response.data.data){
                let customerInfo = response.data.data.info;
                 that.name = customerInfo.name;
                 that.follow_type = customerInfo.type;
                 that.follow_status = customerInfo.status;
                 that.next_follow_time = customerInfo.next_follow_time;
                 that.feature = customerInfo.feature;
                 that.follow_remark = customerInfo.remark;
                 that.deal_money    = customerInfo.deal_money;

                 let columns1Tmp = that.columns1.filter(item=>{
                    return item.value == that.follow_status;
                 })
                 if(columns1Tmp.length>0){
                    that.follow_status_name = columns1Tmp[0].label;
                 }


                 // eslint-disable-next-line no-console
                 console.log(that.columns);

                  let columnsTmp = that.columns.filter(item=>{
                    return item.type == that.follow_type;
                 })
                 if(columnsTmp.length>0){
                    that.follow_type_name = columnsTmp[0].desc;
                 }
             }
          }
        })
        // eslint-disable-next-line no-unused-vars
        .catch(function (error) {});
    },

    // eslint-disable-next-line no-unused-vars
    selectActon(value) {
      this.finished = false;
      this.list = [];
      //  this.onLoad();
    },
    onLoad() {
      // 异步更新数据
      // setTimeout 仅做示例，真实场景中一般为 ajax 请求
      //   let that = this;
      // eslint-disable-next-line no-console
      console.log("submit");
      //   this.$http
      //     .post("/admin/business/lists", {
      //       page_nums: 20,
      //       page: this.page,
      //       status: this.value2,
      //       type: this.value1,
      //       keywords: "",
      //     })
      //     .then(function (response) {
      //       // eslint-disable-next-line no-console
      //       console.log(response.data);
      //       if (response.data.code === 2000) {
      //         //   // 数据全部加载完成
      //         that.list = that.list.concat(response.data.data.list);
      //         // eslint-disable-next-line no-console
      //         console.log(that.list);
      //         if (response.data.data.list.length < 20) {
      //           that.finished = true;
      //         }
      //       }
      //       that.loading = false;
      //     })
      //     // eslint-disable-next-line no-unused-vars
      //     .catch(function (error) {
      //       that.loading = false;
      //       // eslint-disable-next-line no-mixed-spaces-and-tabs
      //       // Toast("登录失败");
      //     });

      // setTimeout(() => {
      //   for (let i = 0; i < 10; i++) {
      //     this.list.push(this.list.length + 1);
      //   }
      //   // 加载状态结束
      //   this.loading = false;

      //   // 数据全部加载完成
      //   if (this.list.length >= 40) {
      //     this.finished = true;
      //   }
      // }, 1000);
    },
    formatPrice() {
      return "¥" + (this.goods.price / 100).toFixed(2);
    },
    onClickCart() {
      this.$router.push("cart");
    },
    sorry() {
      Toast("暂无后续逻辑~");
    },
    onClickLeft() {
      this.$router.back();
    },
    onClickRight() {
      Toast("按钮");
    },
  },
};
</script>

<style lang="less">
.marketDetail {
  padding-bottom: 50px;

  .detilsbox {
    padding: 0 10px;

    .detailcell {
      display: flex;
      padding: 10px 0;
      .title {
        width: 100px;
        color: #9e9e9e;
        font-size: 15px;
      }
      .content {
        color: #424242;
        font-size: 15px;
      }
    }
  }

  .footbox {
    position: fixed;
    bottom: 0;
    display: flex;
    left: 0;
    right: 0;
  }
}

.van-dialog__message{
  text-align: left;
}
</style>
